<template>
    <div class="animate" style="overflow: hidden;height: 100%;">
        <el-row :gutter="20" class="grid-tool-bar">
            <el-col :span="24">
                <el-button size="mini" style="float:left;margin-left: 10px" type="primary" @click="handleCreate()">
                </el-button>
                <el-input style="float:left;width: 150px;margin:0 0 0 20px" placeholder="\\" size="mini"
                          v-model="keyWords.searchWords"></el-input>
                <el-button style="float:left" size="mini" type="primary" @click="loadTable()"></el-button>
            </el-col>
        </el-row>
        <el-table ref="table1" :v-loading="listLoading" :data="userTableData" :height="clientHeight-32" stripe
                  :default-sort="{prop: 'name', order: 'ascending'}" border>
            <el-table-column type="index" label="" width="55"></el-table-column>
            <el-table-column prop="avatarFullPath" align="center" label="" width="90" sortable>
                <template slot-scope="scope">
                    <img v-if="scope.row.avatarFullPath.length>0" :src="scope.row.avatarFullPath" height="40" alt=""/>
                </template>
            </el-table-column>
            <el-table-column prop="loginId" label="" width="120" sortable></el-table-column>
            <el-table-column prop="employeeId" label="" min-width="120" sortable></el-table-column>
            <el-table-column prop="name" label="" width="80" sortable></el-table-column>
            <el-table-column prop="companyName" label="" min-width="120" sortable></el-table-column>
            <el-table-column prop="sex" label="" align="center" min-width="55" sortable>
                <template slot-scope="scope">
                    {{scope.row.sex===1?'':(scope.row.sex===2?'':'')}}
                </template>
            </el-table-column>
            <el-table-column prop="birth" label="" min-width="100" sortable></el-table-column>
            <el-table-column prop="entryDate" label="" min-width="110" sortable></el-table-column>
            <el-table-column prop="workBeginDate" label="" min-width="99" sortable></el-table-column>
            <el-table-column prop="degree" label="" min-width="110" sortable></el-table-column>
            <el-table-column prop="group" label="" min-width="110" sortable></el-table-column>
            <el-table-column prop="qualification" label="" min-width="110" sortable></el-table-column>
            <el-table-column prop="level" label="" min-width="100" sortable></el-table-column>
            <el-table-column prop="postName" label="" min-width="100" sortable></el-table-column>
            <el-table-column prop="role_names" label="" min-width="300" sortable>
                <template slot-scope="scope">
                    <el-tooltip v-for="(item, key, index) in scope.row.role_ids_all_obj" :key="key" :content="item.status ===''?'':''" effect="dark" placement="top">
                        <el-tag style="margin-left: 2px" :type="item.status ===''?'primary':'danger'" size="mini">{{item.name }}</el-tag>
                    </el-tooltip>
                </template>
            </el-table-column>
            <el-table-column prop="isAdmin" label="" width="60" align="center" sortable>
                <template slot-scope="scope">
                    <el-tag :type="scope.row.isAdmin ===1?'':'success'" size="mini">{{scope.row.isAdmin ===1?'':''}}</el-tag>
                </template>
            </el-table-column>
            <!--<el-table-column prop="lastLoginDate" label="" width="170" sortable></el-table-column>-->
            <!--<el-table-column class="avatar-row" prop="avatar" width="60" label="">
            <template slot-scope="scope">
            <img :src="Global.urlPrefix+scope.row.avatar" width="40" height="40" />
            </template>
            </el-table-column>-->
            <el-table-column fixed="right" label="" width="106">
                <template slot-scope="scope">
                    <el-button size="mini" type="text" @click="handleUpdate(scope.row)" plain><i
                            class="el-icon-edit "></i>
                    </el-button>
                    <el-button size="mini" type="text" style="color:red;"
                               @click="handelDelete(scope.row.id,scope.row.loginId,scope.row.isAdmin)" plain><i
                            class="el-icon-remove "></i>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                @current-change="handleCurrentChange"
                :current-page="pageNumber"
                :page-size="pageSize"
                layout=" prev, pager, next, jumper"
                :total="totalRow"
        >
        </el-pagination>
        <el-dialog :title="dialogTitle[formDialogStatus]" :visible.sync="formDialogVisible"
                   :close-on-click-modal="false" append-to-body>
            <el-form :rules="rules" ref="formData" size="mini" :model="form" label-width="5em">
                <el-form-item label="" prop="loginId">
                    <el-input v-model="form.loginId"></el-input>
                </el-form-item>
                <!--<el-form-item v-show="formDialogStatus==='create'" label="" :rules="formDialogStatus==='create'?rules.password:[]" prop="password">-->
                <!--<el-input v-model="form.password"></el-input>-->
                <!--</el-form-item>-->
                <el-form-item label="" :rules="formDialogStatus==='create'?rules.password:[]" prop="password">
                    <el-input v-model="form.password"
                              :placeholder="formDialogStatus==='create'?'':''"></el-input>
                </el-form-item>
                <el-form-item label="" prop="employeeId">
                    <el-input v-model="form.employeeId"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="individualResume">
                    <el-input type="textarea" v-model="form.individualResume"></el-input>
                </el-form-item>
                <el-form-item label="" prop="company">
                    <el-cascader
                            size="mini"
                            :props="cascaderProps"
                            placeholder=""
                            :show-all-levels="false"
                            :options="companyTree"
                            filterable
                            :clearable="false"
                            v-model="this.form.company"
                    ></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="sex">
                    <el-select v-model="form.sex" placeholder="">
                        <el-option v-for="item in sexOptions" :key="item.value" :label="item.label"
                                   :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="birth">
                    <el-date-picker v-model="form.birth" type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="entryDate" class="double-line-label">
                    <el-date-picker v-model="form.entryDate" type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="workBeginDate" class="double-line-label">
                    <el-date-picker v-model="form.workBeginDate" type="date" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="level">
                    <el-input v-model="form.degree"></el-input>
                </el-form-item>
                <el-form-item label="" prop="group">
                    <el-input v-model="form.group"></el-input>
                </el-form-item>
                <el-form-item label="" prop="qualification">
                    <el-input v-model="form.qualification"></el-input>
                </el-form-item>
                <el-form-item label="" prop="level">
                    <el-input v-model="form.level"></el-input>
                </el-form-item>
                <el-form-item label="" prop="post">
                    <el-cascader
                            size="mini"
                            :props="cascaderProps"
                            placeholder=""
                            :show-all-levels="false"
                            :options="postTree"
                            filterable
                            :clearable="false"
                            v-model="form.post"
                    ></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="checkedRoles">
                    <el-checkbox-group v-model="form.checkedRoles">
                        <el-checkbox v-for="role in roleList" :label="role.value" :key="role.value">{{role.text}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item>
                    <image-cropper ref="imageCropper"></image-cropper>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
          <el-button @click="formDialogVisible = false"></el-button>
          <el-button type="primary" @click="postFormData()"></el-button>
                <!--<el-button v-else type="primary" @click="postData()"> </el-button>-->
      </span>
        </el-dialog>
    </div>
</template>

<script>

    import ImageCropper from 'src/components/ImageCropper'
    import {createUser, deleteUser, fetchRoles, fetchUser, fetchUserTable, updateUser} from 'src/api/user'
    import {fetchCompanyList} from 'src/api/company'
    import {fetchPostList} from 'src/api/post'

    import {showNotify} from 'src/utils/tools'
    import Avatar from 'vue-image-crop-upload'

    export default {
        name: 'userOption',
        components: {
            Avatar,
            ImageCropper
        },
        data() {
            return {
                // Global,
                clientHeight: 0,
                listLoading: false,
                userTableData: [],
                formDialogVisible: false,
                dialogTitle: {
                    update: '',
                    create: ''
                },
                formDialogStatus: '',
                form: {
                    checkedRoles: [],
                },
                roleList: [],
                rules: {
                    loginId: [{required: true, message: '', trigger: 'blur'},
                        // {validator: validateLoginId, trigger: 'change'}
                    ],
                    password: [{required: true, message: '', trigger: 'blur'}],
                    name: [{required: true, message: '', trigger: 'blur'}],
                },
                avatarDialogVisible: false,
                previews: {},
                editingAvatarUrl: '',
                sexOptions: [{
                    value: 1,
                    label: ''
                }, {
                    value: 2,
                    label: ''
                }],
                pageNumber: 1,
                pageSize: 50,
                totalRow: 0,
                keyWords: {searchWords: ''},
                postTree: [],
                companyTree: [],
                cascaderProps: {
                    value: 'id',
                    expandTrigger: 'hover',
                    checkStrictly: true
                },
            }
        },
        computed: {
            scale: function () {
                return {x: 60 / this.previews.w, y: 60 / this.previews.h}
            }
        },
        created() {
            this.fullHeight()
            this.loadTable()
        },
        mounted() {
            this.fetchRoleListData();
            this.fetchCompanyData();
            this.fetchPostData();
        },
        methods: {
            fullHeight() {
                this.clientHeight = `${document.documentElement.clientHeight}` - 120;
                const that = this;
                window.onresize = function temp() {
                    that.clientHeight = `${document.documentElement.clientHeight}` - 120;
                };
            },
            loadTable() {
                this.listLoading = true;
                fetchUserTable(this.pageNumber, this.pageSize, this.keyWords).then(response => {
                    this.userTableData = response.data.userTable;
                    this.totalRow = response.data.totalRow;
                    this.listLoading = false;
                })
            },
            handleCreate() {
                this.form = {
                    checkedRoles: [],
                };
                this.formDialogStatus = 'create';
                this.formDialogVisible = true;
                this.$nextTick(function () {
                    this.$refs.imageCropper.clearFile();
                })
            },
            handelDelete(id, loginId, isAdmin) {
                if (isAdmin === 1) {
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    })
                    return;
                }
                this.$confirm(' <span style="color:blue">' + loginId + '</span>?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    dangerouslyUseHTMLString: true
                }).then(() => {
                    this.listLoading = true;
                    deleteUser(id).then(response => {
                        this.listLoading = false;
                        this.loadTable();
                        showNotify(response, '');
                    })
                }).catch(() => {
                    // this.$message({
                    //   type: 'info',
                    //   message: ''
                    // });
                });
            },
            handleUpdate(row) {
                this.listLoading = false
                this.formDialogVisible = true;
                let self = this;
                fetchUser(row.id).then(response => {
                    this.form = response.data.user;
                    this.$nextTick(function () {
                        this.$refs.imageCropper.imgFullPath = self.form.avatarFullPath;
                        this.$refs.imageCropper.imgPath = self.form.imgPath;
                    })
                });
                this.formDialogStatus = 'update';
                // this.$nextTick(() => {
                //   this.$refs['formData'].clearValidate()
                // })
                // this.$refs.table1.tableData //
            },
            postFormData() {
                if (this.$refs.imageCropper.imgPath !== '') {
                    this.form.avatar = this.$refs.imageCropper.imgPath;
                }
                this.$refs['formData'].validate((valid) => {
                    if (valid) {
                        if (this.formDialogStatus === 'update') {
                            updateUser(this.form).then(response => {
                                this.formDialogVisible = false;
                                this.loadTable();
                                showNotify(response, '');
                            })
                        } else if (this.formDialogStatus === 'create') {
                            createUser(this.form).then(response => {
                                this.formDialogVisible = false;
                                this.loadTable();
                                showNotify(response, '');
                            })
                        }
                    }
                })
            },
            // saveAvatar() {
            //   this.$refs.cropper.getCropData((data) => {
            //     // do something
            //     console.log(data)
            //   })
            // },
            // uploadAvatar(e) {
            //   //
            //   // this.option.img
            //   var file = e.target.files[0]
            //   if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
            //     alert('.gif,jpeg,jpg,png,bmp')
            //     return false
            //   }
            //   if (e.target.files[0].size > 1024 * 1024 * 2) {
            //     alert('2M')
            //     return false
            //   }
            //   var reader = new FileReader()
            //   reader.onload = (e) => {
            //     let data
            //     if (typeof e.target.result === 'object') {
            //       // Array Bufferblob base64
            //       data = window.URL.createObjectURL(new Blob([e.target.result]))
            //     } else {
            //       data = e.target.result
            //     }
            //     this.editingAvatarUrl = data
            //   }
            //   // base64
            //   reader.readAsDataURL(file)
            //   // blob
            //   //reader.readAsArrayBuffer(file)
            // },
            // uploadButton() {
            //   document.getElementById("uploads").click();
            // },
            // finish(type) {
            //   this.$refs.cropper.getCropData((data) => {
            //     test.location.href = data
            //   })
            // },

            fetchRoleListData() {
                fetchRoles().then(response => {
                    this.roleList =response.data.roles;
                });
            },
            //companySelector
            fetchCompanyData() {
                fetchCompanyList().then(response => {
                    this.companyTree = response.data.companyList;
                })
            },
            //postSelector
            fetchPostData() {
                fetchPostList().then(response => {
                    this.postTree = response.data.postList;
                })
            },

            handleCurrentChange(val) {
                this.pageNumber = val
                this.loadTable();
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
